$green: #24aa98;
$blue: #65abd0;
$orange: #d9c26c;
$red: #db7560;
$grey: #eaeaea;
$black: #383737;
$white: #fff;

* {
  box-sizing: border-box;
}
a {
  color: $white;
  text-decoration: none;
}

.container {
  max-width: 940px;
  margin: 0 auto;
}
.row {
  display: flex;
  justify-content: space-between;
  margin-top: 25px;
  margin-bottom: 25px;
}
.button-group {
  display: flex;
}

.button {
  font-family: "Cabin", sans-serif;
  font-size: 16px;
  font-weight: 600;
  display: inline-block;
  vertical-align: middle;
  text-align: center;
  cursor: pointer;
  min-width: 140px;
  height: 48px;
  padding: 14px;
  background: $blue;
  color: $white;
  transition-duration: 1s;
  border: 2px solid transparent;
}
.button:hover {
  background: #77bfe5;
  color: $white;
}

.button_large {
  font-size: 18px;
  min-width: 190px;
  height: 60px;
  padding: 18px;
}

.button-small {
  min-width: 120px;
  height: 36px;
  font-size: 14px;
  padding: 8px;
}

.button_radius {
  border-radius: 6px;
}
.button_round {
  border-radius: 50px;
}
.button_circle {
  border-radius: 50%;
  min-width: 48px;
  padding: 0;
}
.button_circle-large {
  min-width: 60px;
  height: 60px;
  padding: 0;
}
.button_circle-small {
  min-width: 36px;
  height: 36px;
}
.button_reverse {
  border: 2px solid $blue;
  background: $white;
  color: $blue;
}
.button_burger {
  min-width: 48px;
  padding: 0;
}
.button_burger-small {
  min-width: 36px;
  height: 36px;
  padding: 0;
}
.button_arrow {
  position: relative;
  min-width: 76px;
  z-index: 1;
  margin-right: 24px;
}
.button_arrow:after {
  content: "";
  position: absolute;
  top: 5px;
  right: -16px;
  width: 30px;
  height: 30px;
  background: inherit;
  border: inherit;
  border-left-color: transparent;
  border-bottom-color: transparent;
  border-radius: 6px;
  -webkit-transform: rotate(45deg);
  transform: rotate(45deg);
  z-index: -1;
}

.button_group {
  display: inline-block;
}
.button-nav {
  min-width: 36px;
  height: 36px;
  padding: 0;
}
.button-nav:first-child {
  border-radius: 6px 0px 0px 6px;
  border-right: 1px solid rgba(0, 0, 0, 0.1);
  margin-right: -2px;
}
.button-nav:last-child {
  border-radius: 0px 6px 6px 0px;
  border-left: 1px solid rgba(0, 0, 0, 0.1);
  margin-left: -2px;
}
.button_arrow-small {
  position: relative;
  min-width: 90px;
  z-index: 1;
  margin-right: 15px;
}
.button_arrow-small:after {
  content: "";
  position: absolute;
  top: 3px;
  right: -11px;
  width: 23px;
  height: 23px;
  background: inherit;
  border: inherit;
  border-left-color: transparent;
  border-bottom-color: transparent;
  border-radius: 6px;
  -webkit-transform: rotate(45deg);
  transform: rotate(45deg);
  z-index: -1;
}
